import React from 'react';
import './memberCard.css';
import Card from './memberMaker';
import img4 from '../../Images/NITAP.png';
import img1 from '../../Images/Committee/Dhanraj2.jpg';
import img2 from '../../Images/Committee/Devika.jpg';

function Cards() {
  return (
        <div className='cards__wrapper3'>
        <ul className='cards__items3'>
            <Card
              src={img2}
              text='Ms. Devika Thota'
              label='Executive Member'
              path='/dr___'
            />
            <Card
              src={img1}
              text='Mr. Bhukya Dhanraj'
              label='Executive Member'
              path='/dr___'
            />
            <Card
              src={img4}
              text='Ms. Sri Harshini Vissamsetti'
              label='Executive Member'
              path='/dr___'
            />
            <Card
              src={img4}
              text='Mr. Velaga Sumanth'
              label='Executive Member'
              path='/dr___'
            />
          </ul>
          
        </div>
  );
}

export default Cards;
