import React from 'react';
import '../../App';
import './eventscard.css';
import img5 from '../../Images/kalabharati.PNG';

function Search (){
  

  return (

    <div className='app-container'>
      <h1>CURRENT EVENTS</h1>
      <div className='centered-event-container'>
      <div className='event-box-container'>
        <img className='event-box-img' src={img5}/>
        <div className='info'><h1>4th Convocation<br/></h1></div><br/><br/>
        <div className='timeline'>10th September 2022 | Offline</div>
        <div className='desc'>Ravindra Kala Bharati, NIT Andhra Pradesh</div>
      </div>
    </div>
    </div>
  );
}

export default Search;