import React,{useState} from "react";
import './facilitate.css';
import { Link } from "react-router-dom";
import axios from 'axios';


const Facilitate = () => {

    

    var session = null;
    session = sessionStorage.getItem('loginSussessful');
    
    /*
    var scholarship = false;
    var mentor = false;
    var career = false;
    var almaconnect = false;
    var recruitment = false;*/
    
    const [scholarship,setScholarship] = useState(false)
    const [mentor,setMentor] = useState(false)
    const [career,setCareer] = useState(false)
    const [almaconnect,setAlmaConnect] = useState(false)
    const [recruitment,setRecruitment] = useState(false)
    const [other,setOther] = useState('');

    const handleSubmit = () => {
        axios.post('http://localhost:3001/facilitateus',{
                    mail : session,
                    scholarship : scholarship,
                    mentor : mentor,
                    career : career,
                    almaconnect : almaconnect,
                    recruitment : recruitment,
                    other : other,
                }).then((result) => {
                    if (result){
                        alert("Preferences Updated");
                        window.location.replace("http://localhost:3000/myprofile")
                    }else{
                        alert("Profile not Updated. Please try again");
                    }
                })
    }


    return(
        <div className='form-container'>
            <div className='form-content'>
                <form className='form' onSubmit={handleSubmit} >
                    <h1>Facilitate Us</h1>
                    <p>How would you like to help us?</p>
                    <div className='form-inputs'>
                    <input
                            type="checkbox"
                            id="scholarship"
                            name="facilitateus"
                            value="Scholarship"
                            onClick = {()=> setScholarship(!scholarship)}
                            />
                    <label className='form-label'>Alumni sourcing for Scholarships</label>
                    </div>

                    <div className='form-inputs'>
                        <input
                            type="checkbox"
                            id="mentor"
                            name="facilitateus"
                            value="Mentor"
                            onClick = {()=> setMentor(!mentor)}
                            />
                            <label className='form-label'>Mentor Students</label>
                    </div>

                    <div className='form-inputs'>
                        <input
                            type="checkbox"
                            id="career"
                            name="facilitateus"
                            value="career"
                            onClick = {()=> setCareer(!career)}
                            />
                            <label className='form-label'>Career Guidance and Counselling</label>
                    </div>

                    <div className='form-inputs'>
                        <input
                            type="checkbox"
                            id="almaconnect"
                            name="facilitateus"
                            value="Almaconnect"
                            onClick = {()=> setAlmaConnect(!almaconnect)}
                            />
                            <label className='form-label'>Organizing ALMAConnect events</label>
                    </div>

                    <div className='form-inputs'>
                        <input
                            type="checkbox"
                            id="recruitment"
                            name="facilitateus"
                            value="recruitment"
                            onClick = {()=> setRecruitment(!recruitment)}
                            />
                            <label className='form-label'>Visiting NIT Andhra Pradesh for recruitment</label>
                    </div>
                    
                    <div className='form-inputs'>
                        <label className='form-label'>Other</label>
                        <input
                            className='form-input'
                            type='text'
                            name='other'
                            placeholder='Enter any other way you would like to help us'
                            onChange = {(e) => setOther(e.target.value)}
                        />
                    </div>

                    <button className='form-input-btn' type='submit' > SUBMIT </button>
                    <span className='form-input-login'> <Link to='/myprofile'>Later</Link></span>
                </form>
            </div>
        </div>
    );
};
export default Facilitate;