import React from 'react';
import './devCard.css';
import Card from './devMaker';
import img5 from '../../Images/img.jfif';

function Cards() {
  return (
    <div className='cards1'>
      <h1>Developer</h1>
      <div className='cards__container1'>
        <div className='cards__wrapper1'>
          <ul className='cards__items1'>
            <Card
              src={img5}
              text='Ritvik G'
              path='http://ritvikg.com/'
            />
            </ul>
          
        </div>
      </div>
    </div>
  );
}

export default Cards;
