import React from 'react';
import '../../App';
import './About.css';
import {Link} from 'react-router-dom';

function About() {
  return ( 
      <>
      <div className='about-container'>
      <h5 >About Us</h5>
      
        <h6>Welcome to NIT Andhra Pradesh Alumni Association.</h6>
        <h6>
        AlmaConnect was founded in the year 2021. This association is a key cornerstone of NIT Andhra Pradesh, and the main aim of this association is to encourage 
        the Alumni to participate in the progress and development of the Institute. The Association aims to conduct Alumni Connect programmes, and 
        strives to be a forum for the students to connect with the Alumni and for disseminating the progress of the institute to the Alumni. <br/>
        The Association also has plans to provide scholarships for outstanding students from economically weaker sections, with support from the 
        Alumni.
        </h6>
        <h3></h3>
        <h6>If you are an alumni of NIT Andhra Pradesh and have not registered with AlmaConnect, please Sign Up <a href='https://forms.gle/e1eS8LCF98d5HeFt9'>here</a></h6>

        <h6>To check out our former members, please click <Link to='/formermembers'>here</Link></h6>
      </div>
  
  </>
  );
}

export default About;