import React from 'react';
import './Footer.css';
import { Link } from 'react-router-dom';
import logo from '../../Images/almaconnectlogo.png';

function Footer() {

  return (
    <div className='footer-container123'>
      <section className='footer-subscription'>
        <p className='footer-subscription-heading'>
          Join the happening and eventful community of NIT Andhra Pradesh Alumni
        </p>
      </section>

      <section class='social-media'>
        <div class='social-media-wrap'>
          <div class='footer-logo'>
            <Link to='/' className='social-logo'>
              <img className='almaconnect-logo' src={logo}/>
            </Link>
          </div>
          <small class='website-rights'>NIT-Andhra@{new Date().getFullYear()}</small>
          <div class='social-icons'>
            <a class='social-icon-link facebook' href="https://www.facebook.com/nitandhrapradesh2015/">
              <i class='fab fa-facebook-f' />
            </a>
            <a class='social-icon-link instagram' href="https://www.instagram.com/nit.andhrapradesh/">
              <i class='fab fa-instagram' />
            </a>
            <a class='social-icon-link youtube' href="https://www.youtube.com/channel/UCrXR6gvnN47WAP2QFoBXWfQ">
              <i class='fab fa-youtube' />
            </a>
            <a class='social-icon-link twitter' href="https://twitter.com/NITANDHRA2015">
              <i class='fab fa-twitter' />
            </a>
            <a class='social-icon-link twitter' href="https://www.linkedin.com/school/national-institute-of-technology-andhra-pradesh/mycompany/">
              <i class='fab fa-linkedin' />
            </a>
          </div>
        </div>
      </section>
    </div>
  );
}

export default Footer;
