import React,{useState} from "react";
import './fundraiser.css';
import { Link } from "react-router-dom";
import bank from '../../Images/bankdetails.PNG';

const MyProfile = () => {


    return(
        <div className='form-container7'>
            <div className='form-content7'>
                <form className='form7'>
                    <h1>Bank Details</h1>
                    <img src={bank}/>
                    <h2>Please upload the donation receipt</h2>
                    <div className='form-input-btn' ><a href='https://forms.gle/ti3FwNAMVK3YaqeP6'><h3>Upload</h3></a></div>
                    <span className='form-input-login'> <Link to='/'>Later</Link></span>
                </form>
            </div>
        </div>

    );
};
export default MyProfile;