import './App.css';
import { BrowserRouter as Router, Switch, Route } from 'react-router-dom';
import Navbar from './Components/Navbar/Navbar';
import Footer from './Components/Footer/Footer';
import Home from './Pages/Home/Home';
import About from './Pages/About/About';
import Events from  './Pages/Events/Events';
import fundraiser from './Components/Fundraiser/fundraiser';
import facilitateus from './Components/FacilitateUs/facilitateus';
import eventsdispfuture from './Components/Events/eventsdispfuture';
import eventsdispast from './Components/Events/eventsdispast';
import eventsdispcurrent from './Components/Events/eventsdispcurrent';
import members from './Pages/Members/Members';

function App() {
  
  return (
    <>
      <Router>
        <Navbar />
        <Switch>
          <Route path='/' exact component = {Home} />
          <Route path='/about' component = {About} />
          <Route path='/events' component = {Events} />
          <Route path='/donate' component = {fundraiser} />
          <Route path='/facilitateus' component = {facilitateus} />
          <Route path="/futureevents" component={eventsdispfuture} />
          <Route path="/currentevents" component={eventsdispcurrent} />
          <Route path="/pastevents" component={eventsdispast} />
          <Route path="/formermembers" component={members} />
        </Switch>
        <Footer />
      </Router>
    </>
  );
}

export default App;
