import React from 'react';
import './Cards.css';
import Card from './Card';
import img2 from '../../Images/20075.jpg';
import img3 from '../../Images/20172.jpg';
import imgS from '../../Images/20167.jpg';
import img4 from '../../Images/nitap2.0.png';
import img5 from '../../Images/Committee/Saiteja.jpg';
import img6 from '../../Images/Committee/Yashasvi.jpg';
import img7 from '../../Images/Committee/Tulasi2.jpg';
import dir from '../../Images/director.PNG';

function Cards() {
  return (
    <div className='cards'>
      <h1>Alumni Committee</h1>
      <div className='cards__container'>
        <div className='cards__wrapper'>
          <ul className='cards__items'>
            <Card
              src={dir}
              text='Prof. Pramod M. Padole'
              label='Hon. Chairman - Director'
              path='https://www.nitandhra.ac.in/main/director-profile'
            />
            </ul>
            <h2>Conveners</h2>
            <ul className='cards__items'>
            <Card
              src={img2}
              text='Dr. Karthick Seshadri'
              label='Dean'
              path = "https://nitandhra.ac.in/dept/cse/20075"
            />
            <Card
              src={imgS}
              text='Dr. Santhosh Kumar Gugulothu'
              label='Associate Dean'
              path='https://nitandhra.ac.in/dept/mech/20167'
            />
          </ul>
          <h2>Team</h2>
          <ul className='cards__items'>
            <Card
              src={img5}
              text='Mr. Saiteja Appala'
              label='General Secretary'
              path='/'
            />
            <Card
              src={img6}
              text='Mrs. Yashasvi K.'
              label='General Secretary'
              path='/'
            />
          </ul>
          <ul className='cards__items'>
            <Card
              src={img4}
              text='Mr. Antrakrate Gupta'
              label='Joint Secretary'
              path='/'
            />
            <Card
              src={img7}
              text='Ms. Thota Tulasi Sai Lakshmi'
              label='Joint Secretary'
              path='/'
            />
          </ul>
        </div>
      </div>
    </div>
  );
}

export default Cards;
