import React from 'react';
import '../../App';
import './eventscard.css';
import img5 from '../../Images/2_3_Convocation.jpg';

function Search (){

  return (

    <div className='app-container'>
      <h1>PAST EVENTS</h1>
      
      <div className='event-box-container' >
        <a href ="https://youtu.be/mBajk0-Pi4c">
            <img className='event-box-img' src={img5}/>
        </a>
        <div className='info'><h1>2-3rd Convocation</h1></div>
        <div className='timeline'>13th November 2021 | Ravindra Kala Bharati</div>
        <div className='desc'>The convocation of 2016 and 2017 BTECH batch. This event was held in Ravindra Kala Bharati but was also live telecasted across YouTube.</div>
      
      </div>
    </div>
  );
}

export default Search;