import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import './Navbar.css';
import logo from '../../Images/nitap2.0.png';
import almaconnectlogo from '../../Images/almaconnectlogo.png';

function Navbar() {
  const [click, setClick] = useState(false);

  const handleClick = () => setClick(!click);
  const closeMobileMenu = () => setClick(false);


  return (
    <>
      <nav className='navbar'>
        <div className='navbar-container'>
          
          <a href={'https://www.nitandhra.ac.in/main/'} className="navbar-logo">
          <img className="img-responsive" src={logo} alt="logo"/>
          </a>
          <li className='nav-txt1' >National Institute of Technology</li>
          <li className='nav-txt4'>NIT Andhra Pradesh</li>
          <li className='nav-txt5'>Alma Connect</li>
          <li className='nav-txt2'> Andhra Pradesh | Alma Connect</li>
          <img className="alma-img" src={almaconnectlogo} alt="logo"/>
          <div className='menu-icon' onClick={handleClick}>
            <i className={click ? 'fas fa-times' : 'fas fa-bars'} />
          </div>
          <ul className={click ? 'nav-menu active' : 'nav-menu'}>
            <li className='nav-item'>
              <Link to='/' className='nav-links' onClick={closeMobileMenu}>
                Home
              </Link>
            </li>
            <li className='nav-item' >
              <Link
                to='/about'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                About
              </Link>
            </li>
            <li className='nav-item' >
              <Link
                to='/events'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Events
              </Link>
            </li>
            <li className='nav-item' >
              <Link
                to='/donate'
                className='nav-links'
                onClick={closeMobileMenu}
              >
                Donate
              </Link>
            </li>
            <li className='nav-item' >
              <a
                href='https://forms.gle/e1eS8LCF98d5HeFt9'
                className='nav-links-mobile'
                onClick={closeMobileMenu}
              >
                <i className='fas fa-solid fa-user ' />
              </a>
              
            </li>
          </ul>
          
        </div>
      </nav>
      </>
  );
}



export default Navbar;
